import React, { Component } from "react";
import { connect } from "react-redux";
import classNames from "classnames";
import Immutable from "immutable";
import FileItem from "./fileItem";
import PropTypes from "prop-types";
import Dialog from "material-ui/Dialog";
import FlatButton from "material-ui/FlatButton";
import RaisedButton from "material-ui/RaisedButton";
import DatePicker from "material-ui/DatePicker";
import TextField from "material-ui/TextField";

class BrowseFileModal extends Component {
  constructor(props) {
    super(props);
  }

  componentWillMount() {
    this.props.loadLocalFiles();
  }
  handleSave = () => {
    this.props.toggleBrowse();
  };

  mockSubmit = e => {
    e.keyCode === 13 && this.handleSave();
  };

  render() {
    const { isBrowsing, toggleBrowse } = this.props;
    const oSavedFiles = this.props.savedFiles.toJS();
    
    const actions = [
      <FlatButton label="Cancel" primary={true} keyboardFocused={true} />,
      <FlatButton
        label="Save"
        primary={true}
        keyboardFocused={true}
        onTouchTap={this.handleSave}
      />
    ];
    return (
      <div>
        <Dialog
          title="Please enter the filename"
          actions={actions}
          modal={false}
          open={isBrowsing}
          onRequestClose={this.handleClose}
          contentStyle={{
            width: "50vw"
          }}
          bodyStyle={{
            display: "flex",
            justifyContent: "center"
          }}
          titleStyle={{
            fontWeight: "300"
          }}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  isBrowsing: makeSelectIsBrowsing(state),
  savedFiles: makeSelectSavedFiles(state)
});

BrowseFileModal.propTypes = {
  isBrowsing: PropTypes.bool,
  toggleBrowse: PropTypes.func,
  savedFiles: PropTypes.object,
  loadLocalFiles: PropTypes.func
};

export default connect()(BrowseFileModal);
